'use strict';

define(['knockout', 'text!./view.html', 'lib/url'], function (ko, template, url) {

    function ViewModel(params) {

        this.route = ko.observable(params.route || '');

        this.route.subscribe(function (val) {

            if (val && val !== 'Navigate:') {
                url.load(val);
            }
        });
    }

    return {
        viewModel: ViewModel,
        template: template
    };
});